import Vue from 'vue'
import axios from 'axios';

// let serverUrl = 'syb'  //本地调试时
// let serverUrl = '..'  //打包部署上线时
var serverUrl = process.env.API_ROOT

let Interface = {
    gainJSON: function (url, callback) {
        return new Promise((resolve, reject) => {
            Vue.http.get(serverUrl + url).then((res) => {
                console.log(res);
                if (res.body.status) {
                    callback(res);
                    resolve(res);
                } else {
                    reject();
                }
            });
        });
    }
}

